import React from 'react'
import './Footer.css'
function Footer(props) {
    function showform(e){
        e.preventDefault();
        props.toggleform()
    }

  return (
    <footer>
        <a href='#'>Conditions gnrales</a>
        <span>/</span>
        <a href='#'>Mentions lgales</a>
        <span>/</span>
        <a onClick={showform} href='#'>Contactez-nous</a>
        <span>/</span>
        <a href='#'>Gestion des cookies</a>
    </footer>
  )
}

export default Footer